import React from "react";
import { useRef, useEffect } from "react";
import c from "clsx";
import PhotoViz from "./components/PhotoViz.jsx";
import useStore from "./store.js";
import Sidebar from "./components/Sidebar.jsx";

import {
  setLayout,
  // sendQuery, // Removed
  // clearQuery, // Removed
  // setXRayMode, // Removed
  toggleSidebar,
} from "./actions.js";

export default function App() {
  const {
    layout,
    isSidebarOpen,
    images,
    targetImage,
    cameraCurrentZ
  } = useStore(state => ({
      layout: state.layout,
      isSidebarOpen: state.isSidebarOpen,
      images: state.images,
      targetImage: state.targetImage,
      cameraCurrentZ: state.cameraCurrentZ
  }));
  
  const selectedImageDescription = targetImage && images
    ? images.find(img => img.id === targetImage)?.description
    : null;

  return (
    <main>
      <PhotoViz />
      <Sidebar />
      
      {selectedImageDescription && (
        <div className={c("description-panel", { visible: !!selectedImageDescription })}>
          <p>{selectedImageDescription}</p>
        </div>
      )}

      {/* Layout Controls */}
      <div className="fixed bottom-4 left-4 flex gap-2 z-20">
        <button
          onClick={() => setLayout('sphere')}
          className={c(
            "px-4 py-2 rounded-lg text-sm font-medium transition-all duration-200",
            layout === 'sphere' 
              ? "bg-blue-500 text-white shadow-lg" 
              : "bg-gray-800 bg-opacity-80 text-gray-300 hover:bg-gray-700"
          )}
        >
          Sphere
        </button>
        <button
          onClick={() => setLayout('grid')}
          className={c(
            "px-4 py-2 rounded-lg text-sm font-medium transition-all duration-200",
            layout === 'grid' 
              ? "bg-blue-500 text-white shadow-lg" 
              : "bg-gray-800 bg-opacity-80 text-gray-300 hover:bg-gray-700"
          )}
        >
          Grid
        </button>
      </div>

      {/* Sidebar Toggle Button */}
      <button
        onClick={toggleSidebar}
        className={c("sidebarButton iconButton", { active: isSidebarOpen })}
        aria-label="Toggle photo list"
        title="Toggle photo list"
      >
        <span className="icon">list</span>
      </button>

      {/* Camera Debug Info */}
      <div style={{
        position: 'fixed',
        bottom: '10px',
        right: '10px',
        backgroundColor: 'rgba(0,0,0,0.5)',
        color: 'white',
        padding: '5px 10px',
        borderRadius: '3px',
        fontSize: '12px',
        zIndex: 10000
      }}>
        Camera Z: {cameraCurrentZ.toFixed(2)} | Layout: {layout}
      </div>
    </main>
  );
}
