import React, { useState, useRef, useEffect } from "react";
import { Billboard } from "@react-three/drei";
import { useFrame } from "@react-three/fiber";
import * as THREE from "three";
import { setTargetImage } from "../actions.js";

const aspectRatio = 16 / 16;
const thumbHeight = 64; // 4x maior que o original
const thumbWidth = thumbHeight * aspectRatio;

console.log("🔥 TAMANHO DAS IMAGENS:", thumbHeight, "x", thumbWidth);

export const PhotoNode = ({
  id,
  position,
  targetImage,
}) => {
  const textureUrl = `${import.meta.env.BASE_URL}assets/images/${id}`;
  
  const meshRef = useRef();
  const groupRef = useRef();
  const [texture, setTexture] = useState(null);
  const [isLoaded, setIsLoaded] = useState(false);
  const [isHovered, setIsHovered] = useState(false);
  
  // Animation state
  const targetPosition = useRef(new THREE.Vector3(...position));
  const currentPosition = useRef(new THREE.Vector3(...position));
  const targetOpacity = useRef(1);
  const currentOpacity = useRef(0);
  const targetScale = useRef(1);
  const currentScale = useRef(1);
  
  // Load texture
  useEffect(() => {
    const loader = new THREE.TextureLoader();
    loader.load(
      textureUrl,
      (loadedTexture) => {
        setTexture(loadedTexture);
        setIsLoaded(true);
      },
      undefined,
      (error) => {
        console.error(`[PhotoNode.jsx] Failed to load texture for ${id}:`, error);
        setIsLoaded(true); // Still mark as loaded to show fallback
      }
    );
  }, [textureUrl, id]);
  
  // Update target position when position prop changes
  useEffect(() => {
    targetPosition.current.set(...position);
  }, [position]);
  
  const isActive = targetImage === id;
  
  // Calculate target values based on state
  useEffect(() => {
    if (!targetImage || isActive) {
      targetOpacity.current = 1;
      targetScale.current = isActive ? 1.2 : 1; // Scale up when active
    } else {
      targetOpacity.current = 0.3;
      targetScale.current = 0.8; // Scale down when not active
    }
  }, [targetImage, isActive]);
  
  // Smooth animations
  useFrame((state, delta) => {
    if (!isLoaded) return;
    
    // Animate position
    if (groupRef.current) {
      currentPosition.current.lerp(targetPosition.current, delta * 3);
      groupRef.current.position.copy(currentPosition.current);
    }
    
    // Animate opacity and scale
    if (meshRef.current && meshRef.current.material) {
      // Opacity animation
      currentOpacity.current = THREE.MathUtils.lerp(
        currentOpacity.current,
        targetOpacity.current,
        delta * 4
      );
      meshRef.current.material.opacity = currentOpacity.current;
      
      // Scale animation
      currentScale.current = THREE.MathUtils.lerp(
        currentScale.current,
        targetScale.current,
        delta * 5
      );
      
      // Apply scale with hover effect
      const hoverScale = isHovered ? 1.1 : 1;
      const finalScale = currentScale.current * hoverScale;
      meshRef.current.scale.setScalar(finalScale);
    }
  });
  
  if (!isLoaded) {
    return null; // Don't render until loaded
  }

  return (
    <group
      ref={groupRef}
      onClick={(e) => {
        e.stopPropagation();
        setTargetImage(id);
      }}
      onPointerEnter={() => setIsHovered(true)}
      onPointerLeave={() => setIsHovered(false)}
    >
      <Billboard>
        <mesh ref={meshRef} scale={[thumbWidth, thumbHeight, 1]}>
          <planeGeometry />
          <meshStandardMaterial
            map={texture}
            color={texture ? "white" : "red"}
            transparent={true}
            opacity={currentOpacity.current}
            side={THREE.DoubleSide}
          />
        </mesh>
      </Billboard>
    </group>
  );
}
